.buttons-slider {
    display: flex;
    cursor: pointer;
    top: 300px;
    position: absolute;
    right: 0px; }
.comparison {
    margin-bottom: 5px;
    &-block {
        background-color: #9ee5fb;
        padding-left: 20px;
        height: 50px;
        padding-top: 8px;
        &__title {
            border-left: 4px solid #1c2931;
            padding-left: 10px;
            color: #1c2931;
            font-size: 16px;
            font-family: Helvetica;
            font-weight: 700;
            padding-top: 5px;
            padding-bottom: 5px; } }
    &-data {
        display: flex;
        justify-content: space-between;
        margin-left: 20px;
        margin-right: 20px;
        margin-top: 15px;
        margin-bottom: 15px;
        &__title {
            font-size: 16px;
            font-family: Helvetica;
            font-weight: 700;
            line-height: 30px; }
        &__text1, &__text2, &__text3 {
            font-size: 16px;
            font-family: Helvetica;
            line-height: 30px; } } }
