.carousel {
    width: 600px;
    height: 337px;
    margin-left: 0;
    margin-top: 20px; }
ul.slides {
  display: block;
  position: relative;
  height: 337px;
  margin: 0;
  padding: 0;
  overflow: hidden;
  list-style: none; }

.slides * {
  user-select: none;
  -ms-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -webkit-touch-callout: none; }

ul.slides input {
  display: none; }

.slide-container {
  display: block; }

.slide-image {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  opacity: 0;
  transition: all .7s ease-in-out;

  img {
    width: auto;
    min-width: 100%;
    height: 100%; } }

.carousel-controls {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
  font-size: 100px;
  line-height: 600px;
  color: #fff;

  label {
    display: none;
    position: absolute;
    padding: 0 20px;
    opacity: 0;
    transition: opacity .2s;
    cursor: pointer; } }

.slide-image:hover + .carousel-controls label {
  opacity: 0.5; }

.carousel-controls {
  label:hover {
    opacity: 1; }

  .prev-slide {
    width: 49%;
    text-align: left;
    left: 0; }

  .next-slide {
    width: 49%;
    text-align: right;
    right: 0; } }

.carousel-dots {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 20px;
  z-index: 999;
  text-align: center;

  .carousel-dot {
    display: inline-block;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #fff;
    opacity: 0.5;
    margin: 10px; } }

input {
  &:checked + .slide-container {
    .slide-image {
      opacity: 1;
      transform: scale(1);
      transition: opacity 1s ease-in-out; }

    .carousel-controls label {
      display: block; } }

  &#img-1:checked ~ .carousel-dots label#img-dot-1, &#img-2:checked ~ .carousel-dots label#img-dot-2, &#img-3:checked ~ .carousel-dots label#img-dot-3, &#img-4:checked ~ .carousel-dots label#img-dot-4, &#img-5:checked ~ .carousel-dots label#img-dot-5, &#img-6:checked ~ .carousel-dots label#img-dot-6 {
    opacity: 1; }

  &:checked + .slide-container .nav label {
    display: block; } }

/*rating */
@import url(//netdna.bootstrapcdn.com/font-awesome/3.2.1/css/font-awesome.css);

fieldset, label {
  margin: 0;
  padding: 0; }

/****** Style Star Rating Widget **** */

.rating {
  border: none;
  float: left;

  > {
    input {
      display: none; }

    label:before {
      margin: 5px;
      font-size: 1.25em;
      font-family: FontAwesome;
      display: inline-block;
      content: "\f005"; }

    .half:before {
      content: "\f089";
      position: absolute; }

    label {
      color: #ddd;
      float: right; }

    input:checked ~ label {
      color: #FFD700; } }

  &:not(:checked) > label:hover {
    color: #FFD700;

    ~ label {
      color: #FFD700; } }

  > {
    input:checked {
      + label:hover, ~ label:hover {
        color: #FFED85; } }

    label:hover ~ input:checked ~ label, input:checked ~ label:hover ~ label {
      color: #FFED85; } } }
